.nominees {
  width: 80%;
  margin: 0 auto;
}

.nominees th {
  border-bottom: 1px solid var(--color-dark-grey);
}

.nominees td {
  border-bottom: 1px solid var(--color-grey);
}

.nominees thead tr th {
  height: 50px;
  font-weight: 450;
  text-align: center;
  border-top: 1px solid var(--color-dark-grey);
}

.nominees tbody tr {
  height: 25px;
}

.nominees tbody tr td {
  padding: 2rem;
  text-align: center;
}

.nominees tbody tr:last-child td {
  border-bottom: 1px solid var(--color-dark-grey);
}

.nominees tr td {
  display: flex;
  justify-content: space-around;
}

.comment {
  font-family: "PT Serif", serif;
  font-weight: 600;
  width: 80%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
}

@media screen and (max-width: 800px) {
  .comment {
    font-size: 12px;
  }

  .nominees thead tr th {
    font-size: 14px;
  }

  .nominees tbody tr td {
    padding: 1.2rem;
  }
}
