:root {
  --color-navy: #2b3072;
  --color-blue: #6d95c2;
  --color-grey: #f9f9f9;
  --color-dark-grey: #c9cbdb;
  --color-white: #ffffff;
  --color-black: #000;
}

* {
  box-sizing: border-box;
}

html,
body,
ul,
li,
dl,
dt,
dd,
table,
th,
td,
address,
p {
  margin: 0;
  padding: 0;
  font-family: "Rubik", sans-serif;
  font-size: 16px;
  color: #2b3072;
  color: var(--color-navy);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-family: "PT Serif", serif;
  font-weight: 400;
  color: #2b3072;
  color: var(--color-navy);
}

html,
body {
  width: 100%;
  height: 100%;
  line-height: 1.5;
}

ul,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

img,
fieldset {
  border: 0;
}

address,
em {
  font-style: normal;
}

caption {
  display: none;
}

a {
  text-decoration: none;
  color: #2b3072;
  color: var(--color-navy);
}

a:hover {
  text-decoration: none;
  color: #2b3072;
  color: var(--color-navy);
}

input {
  border: 1px solid #c9cbdb;
  border: 1px solid var(--color-dark-grey);
  background-color: #f9f9f9;
  background-color: var(--color-grey);
  color: #000;
  color: var(--color-black);
  line-height: 1;
}

